import React, { Component } from "react";
import { Link } from "react-router-dom";
import { compose } from "redux";
import { connect } from "react-redux";
import { firestoreConnect } from "react-redux-firebase";
import Spinner from "../Layouts/Spinner";

class EditClient extends Component {
  constructor(props) {
    super(props);

    this.firstNameInput = React.createRef();
    this.LastNameInput = React.createRef();
    this.EmailInput = React.createRef();
    this.PhoneInput = React.createRef();
    this.BalanceInput = React.createRef();
  }

  onSubmit = e => {
    e.preventDefault();

    const { firestore, client, history } = this.props;

    const updClient = {
      ["First Name"]: this.firstNameInput.current.value,
      ["Last Name"]: this.LastNameInput.current.value,
      Email: this.EmailInput.current.value,
      Phone: this.PhoneInput.current.value,
      Balance:
        this.BalanceInput.current.value === ""
          ? 0
          : this.BalanceInput.current.value
    };

    firestore
      .update({ collection: "Clients", doc: client.id }, updClient)
      .then(history.push("/"));
  };
  render() {
    const { client } = this.props;
    const { disableBalanceOnEdit } = this.props.settings;

    if (client) {
      return (
        <div>
          <div className="row">
            <div className="col-md-6">
              <Link to="/" className="btn btn-primary btn-sm">
                <i className="fas fa-arrow-circle-left" /> &nbsp;Back To
                Dashboard
              </Link>
            </div>
          </div>
          <br />
          <div className="card">
            <div className="card-header">
              <b>Edit Client</b>
            </div>
            <div className="card-body">
              <form onSubmit={this.onSubmit}>
                <div className="form-group">
                  <label htmlFor="firstName">First Name</label>
                  <input
                    type="text"
                    className="form-control"
                    name="First Name"
                    minLength="2"
                    required
                    ref={this.firstNameInput}
                    defaultValue={client["First Name"]}
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="lastName">Last Name</label>
                  <input
                    type="text"
                    className="form-control"
                    name="Last Name"
                    minLength="2"
                    required
                    ref={this.LastNameInput}
                    defaultValue={client["Last Name"]}
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="email">Email</label>
                  <input
                    type="email"
                    className="form-control"
                    name="Email"
                    required
                    ref={this.EmailInput}
                    defaultValue={client.Email}
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="phone">Phone</label>
                  <input
                    type="text"
                    className="form-control"
                    name="Phone"
                    minLength="10"
                    required
                    ref={this.PhoneInput}
                    defaultValue={client.Phone}
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="balance">Balance</label>
                  <input
                    type="text"
                    className="form-control"
                    name="Balance"
                    required
                    ref={this.BalanceInput}
                    defaultValue={client.Balance}
                    disabled={disableBalanceOnEdit}
                  />
                </div>

                <input
                  type="submit"
                  value="Submit"
                  className="btn btn-primary btn-block"
                />
              </form>
            </div>
          </div>
        </div>
      );
    } else {
      return <Spinner />;
    }
  }
}

export default compose(
  firestoreConnect(props => [
    { collection: "Clients", storeAs: "client", doc: props.match.params.id }
  ]),
  connect(({ firestore: { ordered }, settings }, props) => ({
    client: ordered.client && ordered.client[0],
    settings
  }))
)(EditClient);
